article, aside, details, figcaption, figure, footer, header, hgroup, main, nav, section, summary {
  display: block; }

audio, canvas, video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden] {
  display: none; }

html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0; }

a:focus {
  outline: thin dotted; }

a:active, a:hover {
  outline: 0; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

abbr[title] {
  border-bottom: 1px dotted; }

b, strong {
  font-weight: bold; }

dfn {
  font-style: italic; }

hr {
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  height: 0; }

mark {
  background: #ff0;
  color: #000; }

code, kbd, pre, samp {
  font-family: monospace,serif;
  font-size: 1em; }

pre {
  white-space: pre-wrap; }

q {
  quotes: "\201C" "\201D" "\2018" "\2019"; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -0.5em; }

sub {
  bottom: -0.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

figure {
  margin: 0; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

legend {
  border: 0;
  padding: 0; }

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0; }

button, input {
  line-height: normal; }

button, select {
  text-transform: none; }

button, html input[type="button"], input[type="reset"], input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled], html input[disabled] {
  cursor: default; }

input[type="checkbox"], input[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

input[type="search"] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box; }

input[type="search"]::-webkit-search-cancel-button, input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

textarea {
  overflow: auto;
  vertical-align: top; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*,
*::after,
*::before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

body {
  font-family: 'Nunito', monospace;
  font-weight: 600;
  letter-spacing: 1px;
  color: #fff;
  color: #fff;
  background-color: #363dc2;
  background-color: #1d1b22;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

/* Page Loader */
.js .loading::before {
  content: '';
  position: fixed;
  z-index: 10000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #1d1b22; }

.js .loading::after {
  content: '';
  position: fixed;
  z-index: 10000;
  top: calc(50% - 30px);
  left: calc(50% - 30px);
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 10px solid #342560;
  border-left-color: #fff;
  animation: loaderAnim 0.8s linear infinite forwards; }

@keyframes loaderAnim {
  to {
    transform: rotate(360deg); } }

main {
  position: relative;
  width: 100%;
  overflow: hidden; }

a {
  text-decoration: none;
  color: #f9ed8a;
  outline: none;
  letter-spacing: 1px; }

a:hover,
a:focus {
  color: #fff;
  outline: none; }

.hidden {
  position: absolute;
  overflow: hidden;
  width: 0;
  height: 0;
  pointer-events: none; }

/* Icons */
.icon {
  display: block;
  width: 1.5em;
  height: 1.5em;
  margin: 0 auto;
  fill: currentColor; }

/* Deco Shape */
.decoshape {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  fill: #553e9a; }

/* SVG Morph */
.morph-wrap {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none; }

.morph {
  position: relative;
  height: 100%;
  fill: #342560;
  flex: none; }

.content-wrap {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  min-height: 600px;
  pointer-events: none; }

.content {
  position: relative;
  display: grid; }

.content--fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  min-height: 600px;
  z-index: 100;
  pointer-events: none;
  padding: 1.5em;
  grid-template-columns: 50% 50%;
  grid-template-rows: auto auto 4em;
  grid-template-areas: "header ..." "... decotitle" "demos decotitle"; }

.content--fixed a {
  pointer-events: auto; }

.content--layout {
  position: relative;
  max-width: 570px;
  pointer-events: auto;
  padding-left: 1%;
  padding-right: 1%; }

.content__img {
  opacity: 0.8;
  max-width: 100%;
  max-height: 100%; }

.content__title {
  position: absolute;
  top: -10px;
  right: -150px;
  font-family: 'Nunito', sans-serif;
  letter-spacing: -0.025em;
  font-size: 8em;
  margin: 0;
  text-transform: lowercase;
  z-index: 10;
  cursor: default; }

.content__author {
  position: absolute;
  left: 1%;
  bottom: 20px;
  -webkit-writing-mode: vertical-lr;
  writing-mode: vertical-lr;
  text-align: right;
  z-index: 10;
  cursor: default; }

.content__desc {
  position: absolute;
  bottom: 0px;
  right: -150px;
  width: 150px;
  font-weight: 600;
  letter-spacing: 1px;
  z-index: 10;
  cursor: default; }

.content__img,
.content__title,
.content__author,
.content__desc {
  transition: transform 0.3s ease-out; }

.content__link {
  display: block;
  margin-top: 20px;
  pointer-events: auto;
  z-index: 10; }

.content--footer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 30vh;
  min-height: 300px;
  z-index: 1; }
  .content--footer h4 {
    font-size: 40px;
    text-align: center;
    margin-bottom: 0.7em; }
  .content--footer .social {
    display: flex;
    justify-content: center; }
  .content--footer .footer__socials a {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    margin-left: 25px;
    border-radius: 50%;
    background-color: rgba(249, 237, 138, 0.2);
    font-size: 26px;
    color: #fff;
    transition: color 0.6s cubic-bezier(0.23, 1, 0.32, 1); }
    .content--footer .footer__socials a:hover {
      color: #33cc99;
      cursor: pointer; }
  .content--footer .footer__socials .icon {
    width: auto;
    height: auto; }

/* Header */
.header {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  z-index: 100;
  grid-area: header;
  align-self: start; }

/* Demos */
.demos {
  grid-area: demos;
  align-self: end; }

.demo {
  display: inline-block;
  position: relative;
  font-size: 0.85em;
  margin: 0 0.25em 0.5em 0;
  padding: 1em; }

.demo .decoshape {
  display: none; }

.demo--current {
  color: #fff;
  border-color: currentColor; }

.demo--current .decoshape {
  display: block;
  z-index: -1; }

/* Deco elements */
.deco {
  pointer-events: none; }

.deco--title {
  grid-area: decotitle;
  -webkit-writing-mode: vertical-lr;
  writing-mode: vertical-lr;
  text-align: right;
  align-self: end;
  justify-self: end; }

@media screen and (max-width: 50em) {
  .content--fixed {
    height: auto;
    min-height: 0;
    display: block;
    z-index: 1000;
    position: absolute; }
  .header {
    align-items: center;
    flex-direction: column; }
  .deco--title {
    display: none; }
  .demos {
    text-align: center; } }

@media screen and (max-width: 967px) {
  .content-wrap {
    min-height: 300px; }
  .content--layout {
    padding-left: 2%;
    padding-right: 2%; }
  .content__author {
    display: none; }
  .content__title {
    position: static;
    font-size: 5em; }
  .content__desc,
  .content__link {
    font-size: 24px; }
  .content__desc {
    position: static;
    width: 100%; }
    .content__desc a {
      text-align: right; } }

/*logo*/
.logo {
  z-index: 10000;
  width: 200px;
  height: 64px;
  fill: #fff; }
  .logo:hover {
    fill: #ff4e50;
    transition: fill 0.4s ease-in-out; }
  .logo:hover .logo--v {
    fill: tomato;
    transition: fill 0.4s ease-in-out;
    animation: move 0.6s ease-in-out; }

@keyframes move {
  0% {
    transform: rotate(0deg); }
  99% {
    transform: rotate(350deg); }
  100% {
    transform: rotate(-360deg); } }

.pater {
  font-size: 0.65em;
  position: fixed;
  bottom: 0;
  right: 0;
  width: 100%;
  padding: 1.5em;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  background: #553e9a; }

.pater,
.pater:hover {
  color: #fff; }

.pater::after {
  letter-spacing: 0.05em;
  content: 'Sponsor';
  text-transform: uppercase;
  font-size: 0.7em;
  position: absolute;
  top: 0.35em;
  right: 0.5em;
  opacity: 0.5;
  letter-spacing: 0.085em; }

.pater__deco {
  display: none; }

.pater__img {
  display: inline-block;
  width: 100%;
  max-width: 160px;
  position: relative;
  margin: 0 1em 0 0; }

.pater__title {
  display: inline-block;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  text-indent: -0.05em;
  margin: 0;
  position: relative;
  margin: 0.75em 0 0; }

.pater__desc {
  display: none; }

@media screen and (min-width: 50em) {
  .pater {
    text-align: right;
    top: 0;
    bottom: auto;
    width: auto;
    padding: 3em 1.5em 0 0;
    display: block;
    font-size: 0.85em;
    background: transparent; }
  .pater::after {
    top: 1.5em;
    right: 1.5em;
    left: auto;
    font-size: 0.85em; }
  .pater__title {
    display: block;
    margin: 0.75em 0; }
  .pater__desc {
    display: block;
    color: #fff;
    margin: 0;
    position: relative;
    width: 100%;
    max-width: 260px;
    pointer-events: none;
    opacity: 0;
    transform: translate3d(0, -10px, 0);
    transition: opacity 0.3s, transform 0.3s; }
  .pater__img {
    max-width: 200px;
    width: auto;
    margin: 0; }
  .pater:hover .pater__desc {
    opacity: 1;
    transform: translate3d(0, 0, 0);
    transition-delay: 0.2s; }
  .pater__deco {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    fill: #553e9a;
    transform: translate3d(100%, -100%, 0) rotate3d(0, 0, 1, 40deg) scale3d(1, 0.2, 1);
    transition: transform 0.5s ease-out; }
  .pater:hover .pater__deco {
    transform: translate3d(0, 0, 0) scale3d(1, 1, 1); } }
